.pagination-holder {
	margin-top: 30px;
	margin-bottom: 45px;
	ul {
		margin: 0 -3px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	li {
		padding: 0 3px;
		a {
			width: 34px;
			height: 34px;
			line-height: 34px;
			font-size: 15px;
			font-weight: 500;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 5px;
			transition: 0.2s all ease-out;
			cursor: pointer;
		}
		&.active, &:hover {
			a {
				background: var(--primary-color);
				color: #fff;
			}
			svg path {
				fill: #fff;
			}
		}
	}
}